UAAA,CAGA,SAAA,CAJA,QAAA,CAEA,WAGA,CACA,6BAGA,WAAA,CAFA,UAGA","sourcesContent":["<template>\n    <div class=\"start-form mt-5\">\n        <div class=\"section\">\n            <img src=\"/images/5.png\" class=\"form-image\" style=\"width: 100%;\"/>\n        </div>\n        <div class=\"section\">\n            <div class=\"row m-auto justify-content-center align-items-center\" style=\"width: 100%; height: 100%;\">\n                <div class=\"col-12 col-md-12 col-sm-12\">\n                    <div class=\"row align-items-center justify-content-center mx-auto mb-3 travel-card-title-row\" style=\"height:100px;width:100%;\">\n                        <div class=\"col-md-7 p-0\">\n                            <div class=\"row mr-auto ml-0 my-auto travel-card-title\" style=\"position:relative;height:50px;width:250px;\">\n                                <h1 class=\"title-1\">Заявка</h1>\n                                <img src=\"/images/blue-brush.svg\" class=\"blue-brush\" style=\"position: absolute;\"/>\n                            </div>\n                        </div>\n                        <div class=\"col-md-7 p-0\">\n                            <div class=\"row ml-auto mr-0 my-auto travel-card-title\" style=\"position:relative;height:50px;width:250px;\">\n                                <h1 class=\"title-2\"><span>отправлена</span></h1>\n                                <img src=\"/images/orange-brush.svg\" class=\"orange-brush\" style=\"position: absolute;\"/>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div class=\"section\">\n            <div class=\"row m-auto justify-content-center align-items-center\" style=\"width: 100%; height: 100%;\">\n                <p style=\"color: #0f213d; font-family: 'Open Sans'; font-weight: 900\">Пожалуйста, ожидайте, наши менеджеры скоро с Вами свяжутся</p>\n            </div>\n        </div>\n        <div class=\"section\">\n            <button class=\"btn btn-block btn-lg mt-3\" @click=\"newOrder\">Новая заявка</button>\n        </div>\n        <div class=\"section\">\n            <button class=\"btn btn-block btn-lg mt-3\" style=\"background: #f08b23\" @click=\"toHome\">На главную</button>\n        </div>\n    </div>\n</template>\n\n<script>\n    export default {\n        name: \"End\",\n        mounted() {\n            if(this.avia_steps.step3 == false)\n            {\n                this.$router.push('/avia-module')\n            }\n            this.$store.dispatch('changeAviaSteps', {key:'step1', value:false});\n            this.$store.dispatch('changeAviaSteps', {key:'step2', value:false});\n        },\n        computed:\n        {\n            avia_steps() {\n                return this.$store.getters.avia_steps\n            },\n        },\n        methods: {\n            newOrder() {\n                this.$store.dispatch('changeAviaSteps', {key:'step3', value:false});\n                this.$router.push('/avia-module')\n            },\n            toHome() {\n                this.$store.dispatch('changeAviaSteps', {key:'step3', value:false});\n                this.$router.push('/')\n            }\n        }\n    }\n</script>\n\n<style scoped>\n    .start-form {\n        max-width: 500px;\n        margin: auto;\n        margin-top: auto;\n        /*text-align: center;*/\n    }\n    .section {\n        padding: 0 16px;\n    }\n    .start-form p {\n        font-style: italic;\n        font-size: 12px;\n    }\n    .form-button-group {\n        position: fixed;\n        bottom: 0;\n        left: 0;\n        right: 0;\n        width: 100%;\n        padding-left: 16px;\n        padding-right: 16px;\n        background: #FFF;\n        max-height: 56px;\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        padding-bottom: env(safe-area-inset-bottom);\n    }\n    .btn {\n        background: #0f213d;\n        color: white;\n        text-transform: uppercase;\n        font-size: 24px;\n        font-family: \"Open Sans\";\n        font-weight: 900;\n        border: 0;\n        border-radius: 50px;\n        outline: none;\n    }\n     .btn:focus {\n        outline: none !important;\n        box-shadow: none !important;\n    }\n    .title-1 {\n        position: absolute;\n        z-index: 1;\n        left: 50px;\n        top: 10px;\n        color:#062348;\n        font-family: 'Bello Pro' !important;\n        font-size: 35px;\n    }\n    .title-1 span {\n        font-size: 40px;\n    }\n    .title-2 {\n        position: absolute;\n        z-index: 1;\n        left: 52px;\n        top: 18px;\n        color:white;\n        font-family: 'Open Sans' !important;\n        font-weight:900;\n        font-size: 25px;\n    }\n    .title-2 span {\n        text-transform: uppercase;\n    }\n    .orange-brush{\n        top: 5px;\n        left: 10px;\n        width: 265px;\n    }\n    .blue-brush{\n        top: 10px;\n        left: -55px;\n        width: 330px;\n        height: 50px;\n        opacity: 1;\n    }\n    .form-image {\n        width: 100%;\n        /*max-width: 200px;*/\n        height: auto;\n    }\n</style>\n"],"sourceRoot":""}]);
